'use client';

import React, { useEffect, useMemo, useRef, useState } from "react";
import { motion, AnimatePresence, LayoutGroup } from "framer-motion";

/** VISUAL IDEA
 * 1) Raw strings -> list(list(s)), list(list(t))
 * 2) Animated sort of both lists
 * 3) Compare arrays -> banner TRUE / FALSE
 */

const Button = ({ children, onClick, disabled, variant = "default" }: any) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className={`px-3 py-1.5 rounded-lg text-sm font-medium transition border
      ${variant === "accent"
        ? "bg-indigo-500/90 border-indigo-400/20 text-white hover:bg-indigo-500"
        : variant === "ghost"
        ? "bg-transparent border-white/10 text-white hover:bg-white/10"
        : "bg-white/10 border-white/10 text-white hover:bg-white/15 disabled:opacity-50"}`}
  >
    {children}
  </button>
);

type Phase = "idle" | "split" | "sorting" | "compare" | "done";

function useInterval(cb: () => void, delay: number | null) {
  const saved = useRef(cb);
  useEffect(() => void (saved.current = cb), [cb]);
  useEffect(() => {
    if (delay == null) return;
    const id = setInterval(() => saved.current(), delay);
    return () => clearInterval(id);
  }, [delay]);
}

const Chip = ({ id, ch, active }: { id: string; ch: string; active?: boolean }) => (
  <motion.div
    layout
    layoutId={id}
    className={`rounded-md px-4 py-2 text-sm font-semibold shadow min-w-[48px] flex items-center justify-center
                ${active ? "bg-amber-500" : "bg-slate-700"} text-white`}
    initial={{ opacity: 0, scale: 0.9 }}
    animate={{ opacity: 1, scale: active ? 1.06 : 1 }}
    transition={{ type: "spring", stiffness: 500, damping: 30 }}
  >
    {ch}
  </motion.div>
);

export default function AnagramSortVisualizer() {
  // inputs
  const [s, setS] = useState("racecar");
  const [t, setT] = useState("carrace");
  const [speed, setSpeed] = useState(800);

  // derived
  const sChars = useMemo(() => s.split(""), [s]);
  const tChars = useMemo(() => t.split(""), [t]);

  // viz state
  const [phase, setPhase] = useState<Phase>("idle");
  const [i, setI] = useState(0); // step inside sorting
  const [sArr, setSArr] = useState<string[]>([]);
  const [tArr, setTArr] = useState<string[]>([]);
  const [result, setResult] = useState<boolean | null>(null);
  const [playing, setPlaying] = useState(true);

  const AUTO_LOOP = true;

  const reset = () => {
    setPhase("idle");
    setI(0);
    setSArr([]);
    setTArr([]);
    setResult(null);
    setPlaying(true);
  };

  // step machine
  const step = () => {
    if (phase === "idle") {
      setPhase("split");
      setSArr([...sChars]);
      setTArr([...tChars]);
      return;
    }

    if (phase === "split") {
      setPhase("sorting");
      setI(0);
      return;
    }

    if (phase === "sorting") {
      // "sorted(list(s)) / sorted(list(t))" — animate by doing one insertion per tick
      const nextI = i + 1;
      const sSorted = [...sArr].slice(0, nextI).sort();
      const tSorted = [...tArr].slice(0, nextI).sort();

      setSArr((prev) => [...sSorted, ...prev.slice(nextI)]);
      setTArr((prev) => [...tSorted, ...prev.slice(nextI)]);
      setI(nextI);

      if (nextI >= Math.max(sArr.length, tArr.length)) {
        // final sort
        setSArr((prev) => [...prev].sort());
        setTArr((prev) => [...prev].sort());
        setPhase("compare");
      }

      return;
    }

    if (phase === "compare") {
      const ok = sArr.length === tArr.length && sArr.every((c, k) => c === tArr[k]);
      setResult(ok);
      setPhase("done");
      return;
    }
  };

  useInterval(() => step(), playing ? speed : null);

  // Auto-start on mount
  useEffect(() => {
    if (phase === "idle" && s && t) {
      step();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Reset when inputs change
  useEffect(() => {
    if (s && t) {
      reset();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [s, t]);

  // auto-loop after finish
  useEffect(() => {
    if (phase !== "done" || !AUTO_LOOP) return;
    const t = setTimeout(reset, 1400);
    return () => clearTimeout(t);
  }, [phase]);

  // render helpers
  const renderRow = (label: string, arr: string[], highlightIndex?: number) => (
    <section className="rounded-xl border border-white/10 bg-white/5 p-4">
      <div className="mb-2 flex items-center justify-between">
        <h3 className="text-sm uppercase tracking-widest text-white/60">{label}</h3>
        <span className="text-xs text-white/60">
          {phase === "sorting" ? "sorting…" : phase === "split" ? "list(s)" : ""}
        </span>
      </div>
      <div className="flex flex-wrap gap-2 min-h-[48px]">
        {arr.map((ch, idx) => (
          <Chip key={`${label}-${idx}-${ch}`} id={`${label}-${idx}-${ch}`} ch={ch} active={idx === highlightIndex} />
        ))}
      </div>
    </section>
  );

  return (
    <div className="w-full bg-[#0b0e13] text-white p-6 rounded-xl border border-gray-800">
      <div className="mx-auto max-w-5xl space-y-4">
        <header className="flex items-center justify-between flex-wrap gap-4">
          <div>
            <h2 className="text-2xl font-bold">Valid Anagram — Sorting Visualizer</h2>
            <p className="text-white/60 text-sm mt-1">
              Algorithm shown: <code className="bg-white/10 px-2 py-1 rounded">sorted(list(s)) == sorted(list(t))</code>
            </p>
          </div>
          <div className="flex items-center gap-2">
            <Button onClick={() => setPlaying((p) => !p)} variant="accent">
              {playing ? "Pause" : "Play"}
            </Button>
            <Button onClick={step} disabled={phase === "done"}>Step</Button>
            <Button onClick={reset} variant="ghost">Reset</Button>
          </div>
        </header>

        {/* Inputs */}
        <div className="flex flex-wrap gap-3 items-center">
          <label className="text-sm text-white/70">s:</label>
          <input
            value={s}
            onChange={(e) => setS(e.target.value)}
            className="rounded-lg bg-white/5 border border-white/10 px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500/50"
          />
          <label className="text-sm text-white/70">t:</label>
          <input
            value={t}
            onChange={(e) => setT(e.target.value)}
            className="rounded-lg bg-white/5 border border-white/10 px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-indigo-500/50"
          />
          <label className="text-sm text-white/70">Speed</label>
          <input
            type="range"
            min={200}
            max={1500}
            step={100}
            value={speed}
            onChange={(e) => setSpeed(parseInt(e.target.value))}
            className="w-32"
          />
        </div>

        <LayoutGroup>
          {/* Stage 1 — strings */}
          <section className="rounded-xl border border-white/10 bg-transparent p-4">
            <div className="grid grid-cols-2 gap-4">
              <div>
                <div className="text-xs text-white/60 mb-1">s (string)</div>
                <div className="rounded-lg bg-slate-800/80 px-3 py-2 font-mono">{s}</div>
              </div>
              <div>
                <div className="text-xs text-white/60 mb-1">t (string)</div>
                <div className="rounded-lg bg-slate-800/80 px-3 py-2 font-mono">{t}</div>
              </div>
            </div>
          </section>

          {/* Arrow */}
          <div className="flex items-center justify-center my-2">
            <motion.div
              initial={{ opacity: 0, y: -6 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-white/60 text-sm"
            >
              ▼ convert with <code>list()</code>
            </motion.div>
          </div>

          {/* Stage 2 — lists + sorting */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {renderRow("list(s)", sArr, phase === "sorting" ? i : undefined)}
            {renderRow("list(t)", tArr, phase === "sorting" ? i : undefined)}
          </div>

          {/* Arrow */}
          <div className="flex items-center justify-center my-2">
            <motion.div
              initial={{ opacity: 0, y: -6 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-white/60 text-sm"
            >
              ▼ apply <code>sorted()</code> to both
            </motion.div>
          </div>

          {/* Result */}
          <AnimatePresence>
            {phase === "done" && result !== null && (
              <motion.div
                initial={{ y: 20, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                exit={{ opacity: 0 }}
                className={`mt-2 rounded-xl border p-4 text-center text-lg font-semibold ${
                  result
                    ? "bg-emerald-500/10 border-emerald-400/20 text-emerald-200"
                    : "bg-rose-500/10 border-rose-400/20 text-rose-200"
                }`}
              >
                {result ? "sorted(list(s)) == sorted(list(t)) → True (anagram)" :
                           "Not equal → False (not an anagram)"}
              </motion.div>
            )}
          </AnimatePresence>
        </LayoutGroup>

        <footer className="text-xs text-white/60 mt-4 grid gap-1">
          <p><b>Time:</b> O(n log n) for the sorts. <b>Space:</b> O(n).</p>
          <p>Try examples: s=<code>"jar"</code>, t=<code>"jam"</code> or s=<code>"anagram"</code>, t=<code>"nagaram"</code>.</p>
        </footer>
      </div>
    </div>
  );
}
